import React, { Component } from 'react';
import firebase from '.././firebase';

class Entry extends Component {
  constructor() {
    super();
    this.state = {
      journalData: []
    }
  }

  componentDidMount() {
    const data = firebase.database().ref('entries');

    data.on('value', (response) => {
      const dataFromEntries = response.val();

      const stateToBeSet = [];

      for (let key in dataFromEntries) {
        const data = {
          key: key,
          name: dataFromEntries[key],
        }
        stateToBeSet.push(data);
      }

      this.setState({
        journalData: stateToBeSet,
      }, () => {
        console.log(this.state.journalData);
      })
    })

  }

  showSelectedEntry = (index) => {
    const originalData = [...this.state.journalData];
    console.log(originalData);
    const selectedEntry = originalData.filter((entry, i) => {
      if ( i === index ) {
        return true;
      } else {
        return false;
      }
    })

    this.setState({
      journalData: selectedEntry,
    })
  }

  render() {
    return (
      <section className="singleEntry">
        {this.state.journalData.map((entry, index) => {
          return (
            <div>
              <h5>{entry.name.title}</h5>
              <p>{entry.name.date}</p>
              <p>{entry.name.location}</p>
              <p>{entry.name.text}</p>
              <img src={entry.name.photo} alt="" />
            </div>
          );
        })}
      </section>
    );
  }
}

export default Entry;